import { useEffect, useState } from "react";
import service_lock from "../assets/service_lock.svg";
import { url } from "../components/Context";
import Footer from "../components/Footer";
import Navbar from "../components/Navbar";
import PageHero from "../components/PageHero";
import "../styles/WhatWeDo.css";

export default function WhatWeDo() {
    const [banner, setBanner] = useState([]);
    const [cards, setCards] = useState([]);

    useEffect(() => {
        window.scrollTo({ top: 0, behavior: 'smooth' });

        fetch(`${url}service/`)
            .then(response => response.json())
            .then(data => {
                setBanner(data.data.banner);
                setCards(data.data.cards);
            })
    }, [])

    return (
        <div className="what_we_do_section">
            <Navbar />
            {/* <MessengerCustomerChat /> */}
            {banner.length > 0 && <PageHero image={`${url}media/${banner[0].fields.image}`} name={banner[0].fields.heading} tagline={banner[0].fields.text} showLogo />}
            

            <div className="services_details d-flex flex-wrap justify-content-center align-items-start my-5">

                {cards.length > 0 && cards.map(item => (
                    <div className="service_card d-flex flex-column">
                        <img src={`${url}media/${item.fields.image}`} alt="" />

                        <div className="service_heading d-flex align-items-center">
                            <img src={service_lock} alt="" />
                            <h3>{item.fields.heading}</h3>
                        </div>

                        <p>{item.fields.text}</p>
                    </div>
                ))}

            
            </div>

            <Footer />
        </div>
    )
}