import "../styles/Card.css";
import CustomButton from "./CustomButton";

export default function Card(props) {
    function first27Words(str) {
        let words = str.split(" ");
        return words.slice(0, 20).join(" ");
    }

    let shortText;

    if (props.short) {
        shortText = first27Words(props.text);
    }

    return (
        <div className="card_body d-flex flex-column justify-content- align-items-center">
            <img src={`${props.thumbnail}`} alt="thumb" />
            <div className="card_info d-flex flex-column justify-content-between align-items-start">
                <h2>{props.heading}</h2>
                {props.short ? (<p>{`${shortText}....`}</p>) : <p>{props.text}</p>}
                {/* max 27 words */}
                <CustomButton link={props.link} label={props.btn_label} />
            </div>
        </div>
    )
}