import { useEffect, useState } from "react";
import { url } from "../components/Context";
import Footer from "../components/Footer";
import Navbar from "../components/Navbar";
import PageHero from "../components/PageHero";
import "../styles/About.css";

export default function About() {
    const [banner, setBanner] = useState([]);
    const [mission, setMission] = useState([]);
    const [vision, setVision] = useState([]);
    
    useEffect(() => {
        window.scrollTo({ top: 0, behavior: 'smooth' });

        fetch(`${url}about_us/`)
            .then(response => response.json())
            .then(data => {
                setBanner(data.data.banner);
                setMission(data.data.mission);
                setVision(data.data.vision);
            })
    }, [])

    return (
        <>
        <Navbar />

        {/* <MessengerCustomerChat /> */}

        {banner.length > 0 && <PageHero showLogo image={`${url}media/${banner[0].fields.image}`} name={banner[0].fields.heading} tagline={banner[0].fields.text} />}
        

        <div className="container py-5">
            {mission.length > 0 && (
            <div className="row">

                <div className="col-md-6 about_box d-flex flex-column justify-content-center align-items-start my-2 py-2 px-4">
                    <h1>{mission[0].fields.heading}</h1>
                    <p>{mission[0].fields.text}</p>
                </div>
                
                <div className="page_image col-md-6 d-flex justify-content-center align-items-center my-2 py-2 px-4">
                    {/* <BackedImage image={mission_image} right /> */}
                    <img className="img-fluid" src={`${url}media/${mission[0].fields.image}`} alt="" />
                </div>
            </div>
            )}
        </div>
        
        <div className="container py-5">
            {vision.length > 0 && (
            <div className="row">
                <div className="page_image col-md-6 d-flex justify-content-center align-items-center my-2 py-2 px-4">
                    {/* <BackedImage image={vision_image} right /> */}
                    <img className="img-fluid" src={`${url}media/${vision[0].fields.image}`} alt="" />
                </div>
                
                <div className="col-md-6 about_box d-flex flex-column justify-content-center align-items-start my-2 py-2 px-4">
                    <h1>{vision[0].fields.heading}</h1>
                    <p>{vision[0].fields.text}</p>
                </div>
                
            </div>
            )}
        </div>

        <Footer />
        </>
    )
}