import { useState } from "react";
import { Link } from "react-router-dom";
import hamburger from "../assets/hamburger.svg";
import logo from "../assets/Logo.svg";
import "../styles/Navbar.css";

export default function Navbar() {
    const [toggle, setToggle] = useState(false)

    function toggleHamburger() {
        setToggle(!toggle);
    }

    return (
        <>
        <div className="container-fluid shadow nav_sticky w-100">
            <div className="nav_main container d-flex justify-content-between align-items-center">
                <div className="nav_brand">
                    <img src={logo} alt="logo_bengal_troops" />
                </div>

                <div className="nav_links d-flex justify-content-center align-items-center">
                    <Link to="/">Home</Link>
                    <Link to="/about">About Us</Link>
                    <Link to="/what-we-do">What We Do</Link>
                    <Link to="/contact"><span id="nav_contact">Contact us</span></Link>
                </div>

                <div className="nav_hamburger">
                    <img onClick={toggleHamburger} src={hamburger} alt="hamburger" />
                </div>
            </div>
        </div>

        {/* phone navbar */}

        <div className={`phone_nav ${!toggle ? "off" : ""} d-flex flex-column justify-conetent-center align-items-center`}>
            <Link to="/">Home</Link>
            <Link to="/about">About Us</Link>
            <Link to="/what-we-do">What We Do</Link>
            <Link to="/contact"><span id="phone_nav_contact">Contact us</span></Link>
        </div>
        </>
    )
}