import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import facebook from "../assets/facebook.svg";
import instagram from "../assets/instagram.svg";
import logo from "../assets/Logo.svg";
import twitter from "../assets/twitter.svg";
import youtube from "../assets/youtube.svg";
import "../styles/Footer.css";
import { url } from "./Context";

export default function Footer() {
    const [footerText, setFooterText] = useState([]);
    const [services, setServices] = useState([]);
    const [email, setEmail] = useState('');
    const [message, setMessage] = useState('');

    useEffect(() => {
        fetch(`${url}footer/`)
            .then(response => response.json())
            .then(data => {
                setFooterText(data.data.footer);
                setServices(data.data.services);
            })
    }, [])

    function handleSubmit(event) {
        event.preventDefault();
        
        let body = {
            'email': email
        }

        fetch(`${url}add_subscription/`, {
            method: 'POST',
            headers: {
                'Content-type': 'application/json'
            },
            body: JSON.stringify(body)
        }).then(response => response.json())
        .then(data => {
            setMessage(data.message);
            alert("Thanks for Subscribing!");
            setEmail('');
        })
        .catch(error => console.error(error))
    }

    return (
        <>
        <div className="container-fluid footer_main">
            <div className="container">
                <div className="row">
                    <div className="col-md-4 mt-3">
                        <div className="d-flex footer_1st flex-column justify-content-center align-items-start">
                            <img src={logo} alt="" />
                            {footerText.length > 0 && <p className="company_desc">{footerText[0].fields.footer_text}</p>}
                        </div>
                    </div>

                    <div className="col-md-2 mt-3">
                        <div className="d-flex footer_2nd flex-column justify-content-center align-items-start">
                            <h2 className="mb-4">Services</h2>
                            {services.length > 0 && services.map(item => (
                                <Link to="/what-we-do">{item.fields.heading}</Link>
                            ))}
                        </div>
                    </div>
                    
                    <div className="col-md-2 mt-3">
                        <div className="d-flex footer_2nd flex-column justify-content-center align-items-start">
                            <h2 className="mb-4">Company</h2>
                            <Link to="/">Home</Link>
                            <Link to="/about">About Us</Link>
                            <Link to="/contact">Contact Us</Link>
                            <Link to="/what-we-do">What We Do</Link>
                        </div>
                    </div>

                    <div className="col-md-4 mt-3">
                        <div className="d-flex footer_last flex-column justify-content-center align-items-start">
                            <h2 className="mb-4">Stay With Us</h2>

                            <form onSubmit={handleSubmit} className="form-group subscription_form">
                                <input onChange={(event) => setEmail(event.target.value)} value={email} className="form-control" type="email" placeholder="Email" required />
                                <button type="submit">SUBSCRIBE</button>
                            </form>

                            <div className="d-flex flex-column justify-content-center align-items-start mt-4">
                                <h3>Connect With Us</h3>
                                
                                {footerText.length > 0 ? (
                                    <div className="d-flex justify-content align-items-center icons">  
                                        <a href={footerText[0].fields.facebook_link} target="blank"><img src={facebook} alt="" /></a>
                                        <a href={footerText[0].fields.twitter_link} target="blank"><img src={twitter} alt="" /></a>
                                        <a href={footerText[0].fields.instagram_link} target="blank"><img src={instagram} alt="" /></a>
                                        <a href={footerText[0].fields.youtube_link} target="blank"><img src={youtube} alt="" /></a>
                                    </div>
                                ) : (<p>Loading...</p>)}

                            </div>
                        </div>
                    </div>
                </div>
            <div className="mt-5 copyright_holder d-flex justify-content-start align-items-center">
                <p className="copyright">&#169; 2023 Bengal troops security services Ltd.</p>
            </div>
            </div>
        </div>

        
        <div className="credit d-flex justify-content-center align-items-center">
            Designed and Developed @ <a href="https://piagency.tech" target="blank">&nbsp;PiAgency</a>
        </div>
        </>
    )
}