import { useEffect, useState } from "react";
import location from "../assets/location.svg";
import logo from "../assets/Logo.svg";
import mail from "../assets/mail.svg";
import phone from "../assets/phone.svg";
import ContactForm from "../components/ContactForm";
import { url } from "../components/Context";
import Footer from "../components/Footer";
import Navbar from "../components/Navbar";
import PageHero from "../components/PageHero";
import "../styles/Contact.css";

export default function Contact() {
    const [banner, setBanner] = useState([]);
    const [numbers, setNumbers] = useState([]);
    const [contactInfo, setContactInfo] = useState([]);

    useEffect(() => {
        window.scrollTo({ top: 0, behavior: 'smooth' });

        fetch(`${url}contact/`)
            .then(response => response.json())
            .then(data => {
                setBanner(data.data.banner);
                setNumbers(data.data.numbers);
                setContactInfo(data.data.contact_info);
            })
    }, [])

    return (
        <>
        <Navbar />
        {/* <MessengerCustomerChat /> */}
        
        {banner.length > 0 && <PageHero image={`${url}media/${banner[0].fields.image}`} name="Contact Us" />}

        <div className="contact_page_info">
            <div className="container">
                <div className="contact_page_heading d-flex justify-content-center align-items-center">
                    <h1>We’d love to hear from you</h1>
                </div>

                <div className="row contact_page_info_box">
    
                    <div className="col-md-4 d-flex flex-column justify-content-start align-items-center">
                        <img src={location} alt="location_img" />
                        {contactInfo.length > 0 && <p>{contactInfo[0].fields.full_address}</p>}
                        
                    </div>
    
                    <div className="col-md-4 d-flex flex-column justify-content-start align-items-center">
                        <img src={mail} alt="location_img" />
                        {contactInfo.length > 0 && <p>{contactInfo[0].fields.email_address}</p>}
                    </div>
    
                    <div className="col-md-4 d-flex flex-column justify-content-start align-items-center">
                        <img src={phone} alt="location_img" />
                        {numbers.length > 0 && numbers.map(item => <p>{item.fields.number}</p>)}
                    </div>
    
                </div>
            </div>
        </div>

        <div className="contact_contact_box">
            <div className="container">
                <div className="contact_page_heading d-flex flex-column justify-content-center align-items-center">
                    <h1>We’d love to hear from you</h1>
                    <p>Get in touch</p>
                </div>

                <div className="row contact_form_section">
                    <div className="contact_page_left col-md-6 d-flex flex-column justify-content-center align-items-center my-2">
                        <img src={logo} alt="" />
                        <h3>Let’s start a conversation</h3>
                        <p>We’d love to hear your opinion. When you need our support don’t think to send us a message. We always care about our client's emergencies and security.</p>
                    </div>

                    <div className="col-md-6 d-flex justify-content-center align-items-center my-2">
                        <ContactForm />
                    </div>
                </div>
            </div>
        </div>

        <Footer />
        </>
    )
}