import logo from "../assets/Logo.svg";
import "../styles/PageHero.css";

export default function PageHero(props) {
    return (
        <>
        <div className="page_hero">
            <img src={props.image} alt="hero_image" />
            <div className="page_hero_text d-flex flex-column justify-content-center align-items-center">
                <h1>{props.name}</h1>
                <p>{props.tagline}</p>
            </div>
        </div>

        {props.showLogo && <div className="middle_logo d-flex justify-content-center align-items-center">
            <img src={logo} alt="logo" />
        </div>}
        </>

    )
}