import { useState } from "react";
import "../styles/ContactForm.css";
import { url } from "./Context";

export default function ContactForm(props) {
    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [number, setNumber] = useState('');
    const [message, setMessage] = useState('');

    function handleSubmit(event) {
        event.preventDefault();
        
        let body = {
            'name': name,
            'email': email,
            'number': number,
            'message': message
        }

        fetch(`${url}send/`, {
            method: 'POST',
            headers: {
                'Content-type': 'application/json'
            },
            body: JSON.stringify(body)
        }).then(response => response.json())
        .then(data => console.log(data))
        .catch(error => console.error(error))

        setName('');
        setEmail('');
        setNumber('');
        setMessage('');

        alert("Message sent successfully!");
    }

    return (
        <div className={`contact_form ${props.coloured ? "coloured" : "white"}`}>
            <form onSubmit={handleSubmit} action="" className="form-group">
                <p>Name</p>
                <input onChange={(event) => setName(event.target.value)} value={name} className="form-control" type="text" required />
                
                <p>Email</p>
                <input onChange={(event) => setEmail(event.target.value)} value={email} className="form-control" type="email" required />
                
                <p>Phone</p>
                <input onChange={(event) => setNumber(event.target.value)} value={number} className="form-control" type="number" required />
                
                <p>Message</p>
                <textarea onChange={(event) => setMessage(event.target.value)} value={message} className="form-control" type="text" required rows="5" />

                <button type="submit">SUBMIT</button>
            </form>
        </div>
    )
}